:root {
	--blue: #5C99D1;
	--light-grey: #DFDFDF;
	--dark-grey: #444444;
}

@font-face {
	font-family: 'Avenir Next';
	font-weight: normal;
	font-style: normal;
	font-display: swap;
	src: local('Avenir Next'), local('Avenir-Next'),
		url(/News-Feed/static/media/AvenirNextLTPro-Regular.b5ed9330.otf) format("opentype");
}

@font-face {
	font-family: 'Avenir Next';
	font-weight: bold;
	font-style: normal;
	font-display: swap;
	src: local('Avenir Next Bold'), local('Avenir-Next-Bold'),
		url(/News-Feed/static/media/AvenirNextLTPro-Bold.91d8ff8a.otf) format("opentype");
}

body {
	margin: 0;
	font-family: "Avenir Next", Arial, sans-serif;
	color: #444444;
	color: var(--dark-grey);
}

header {
	background: #5C99D1;
	background: var(--blue);
	padding: 20px 0px;
	color: #FFFFFF;
	margin-bottom: 10px;
}

section {
	padding-bottom: 20px;
}

/* For the webpage title */
h1 {
	font-size: 54px;
	margin: 0;
}

.filter-group {
	display: grid;
	grid-template-columns: 1fr 2fr;
	align-items: center;
	margin-bottom: 10px;
	max-width: 400px;
}

label {
	font-weight: bold;
	font-size: 18px;
}

select {
	font-family: "Avenir Next", Arial, sans-serif;
	background-color: white;
	color: #444444;
	color: var(--dark-grey);
	font-size: 16px;
}

/* For the article titles */
a {
	color: #5C99D1;
	color: var(--blue);
	text-decoration: none;
	-webkit-transition: all 0.3s ease-out;
	transition: all 0.3s ease-out;
	font-weight: bold;
}

a:hover {
	color: #444444;
	color: var(--dark-grey);
	text-decoration: none;
}

.article {
	border-bottom: 1px #bbb solid;
}

/* Adding the horizontal lines */
.article:last-child {
	border-bottom: none;
}

.container {
	padding: 1em;
}

.loader {
    border: 16px solid #DFDFDF;
    border: 16px solid var(--light-grey);
    border-top: 16px solid #5C99D1;
    border-top: 16px solid var(--blue);
    border-radius: 50%;
    width: 150px;
    height: 150px;
    -webkit-animation: spin 2s linear infinite;
            animation: spin 2s linear infinite;
    margin: auto;
    margin-top: 2em;
}

@-webkit-keyframes spin {
    0%		{-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    100%	{-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}

@keyframes spin {
    0%		{-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    100%	{-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}

/* For screens larger than phones */
/* More spacing on left & right & articles are broken into 2 columns */
@media(min-width: 586px){
	header, section {
		display: grid;
		grid-template-columns: 1fr minmax(auto, 60em) 1fr;
	}

	.container {
		grid-column: 2;
	}

	.article-list {
		display: grid;
		grid-template-columns: 1fr 1fr;
	}

	.article {
		padding: 1em;
	}

	/* Adding the vertical lines */
	.article:nth-child(odd) {
		border-right: 1px #bbb solid;
	}

	/* Grabs second last child if it's also odd */
	.article:nth-child(odd).article:nth-last-child(2) { 
		border-bottom: none;
	}
}
